<template>
  <div class="nav">
  	<mt-tabbar class="bar" v-model="selected">
		  <mt-tab-item id="">
		  	<img slot="icon" src="../../static/img/fire.png">
		    
		  </mt-tab-item>
		  <mt-tab-item id="">
		  	<img slot="icon" src="../../static/img/recommend.png">
		    
		  </mt-tab-item>
		  <mt-tab-item id="">
		  	<img slot="icon" src="../../static/img/find.png">
		    
		  </mt-tab-item>
		  <mt-tab-item id="">
		  	<img slot="icon" src="../../static/img/my.png">
		    
		  </mt-tab-item>
		</mt-tabbar>
</div>
</template>
<script>
import { Toast } from 'mint-ui';
import axios from 'axios'
import { Tabbar, TabItem } from 'mint-ui'
export default {
  data () {
    return {
    	selected:'',
    }
  },
  methods:{
  	
  },
  watch:{
  	selected: function (val, oldVal) {
      //  val 
      if (val=='') {
      	this.$router.push({
      		path:'/index/recommend'
      	})
      }
      else if (val=='') {
      	this.$router.push({
      		path:'/index/fire'
      	})
      }
      else if (val=='') {
      	this.$router.push({
      		path:'/index/find'
      	})
      }
      else if (val=='') {
      	this.$router.push({
      		path:'/index/my'
      	})
      }
    }
  }
}
</script>

<style scoped>
	.nav{
	}
	.bar{
		background: #e6e6e6;
	}
	.mint-tabbar > .mint-tab-item.is-selected{
		    background-color: #cccdd2;
	}
</style>












